var mobe = mobe || {};

mobe.chooseType = new function() {

    this.items = document.querySelectorAll('.learnchip-type .lt-item');

    this.init = function() {
        var self = this;
        self.items.forEach(function(item){

            item.addEventListener('click', function() {
                item.classList.toggle('selected');
            });
        });
    };
}

document.addEventListener('DOMContentLoaded', function() {
    mobe.chooseType.init();
}, false);
